<template>
  <div id="app">
    <form @submit.prevent="click" style="height: 100px;position: fixed;">
      <textarea style="width: 500px;height: 50px;" type="text" v-model="input" />
      <input type="submit" />
    </form>

    <div style="padding-top: 100px;">
      <span v-for="(item, index) in list" :key="index" style="white-space: pre-wrap;">
        {{ item }}
      </span>
    </div>
  </div>
</template>

<script>
import { Configuration, OpenAIApi } from 'openai'

export default {
  name: 'App',

  data() {
    return {
      input: '',
      openai: null,
      list: []
    }
  },
  methods: {
    click() {
      this.getRes().then(res => res.data).then(res => {
        if (!res) return
        const text = res.choices && res.choices.length && res.choices[0].text.trim()
        console.log(text);
        this.list.push(text)
      })
    },
    async getRes() {
      return await this.openai.createCompletion({
        model: "text-davinci-003",
        prompt: this.input,
        temperature: 0,
        max_tokens: 4051,
      });
    },
    // async getModels() {
    //   return await this.openai.listModels();
    // }
  },
  created() {
    const configuration = new Configuration({
      apiKey: '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',
    });
    this.openai = new OpenAIApi(configuration);
    this.getModels()
  },
}
</script>

